import {CalculatorCountingRow, CalculatorCountingSection, CalculatorInputWrapper} from "./styled";
import {NumberInput, Text} from "../common";
import React from "react";

type CalculatorSectionProps = {
  numbersTitle: string;
  numberCountOnChange: (value : number | null) => void;
  smsCountOnChange: (value : number | null) => void;
  mmsCountOnChange: (value : number | null) => void;
};

const CalculatorSection = (
  {
    numbersTitle,
    numberCountOnChange,
    smsCountOnChange,
    mmsCountOnChange
  } : CalculatorSectionProps) => (
  <CalculatorCountingSection>
    <CalculatorCountingRow>
      <Text.Regular.Bold.md>
        {numbersTitle}
      </Text.Regular.Bold.md>
      <CalculatorInputWrapper>
        <NumberInput onChange={numberCountOnChange}/>
      </CalculatorInputWrapper>
    </CalculatorCountingRow>
    <CalculatorCountingRow>
      <Text.Regular.Normal.md>
        Outbound SMS
      </Text.Regular.Normal.md>
      <CalculatorInputWrapper>
        <NumberInput onChange={smsCountOnChange} />
      </CalculatorInputWrapper>
    </CalculatorCountingRow>
    <CalculatorCountingRow>
      <Text.Regular.Normal.md>
        Outbound MMS
      </Text.Regular.Normal.md>
      <CalculatorInputWrapper>
        <NumberInput onChange={mmsCountOnChange} />
      </CalculatorInputWrapper>
    </CalculatorCountingRow>
  </CalculatorCountingSection>
);

export default CalculatorSection;
